import { HashRouter as Router, Routes, Route, Link, NavLink, useLocation } from "react-router-dom";
import { useEffect } from "react";
import Home from "./pages/Home";
import IncomeCalc from "./pages/IncomeCalc";
import IncomeGraph from "./pages/IncomeGraph";
import "./App.css";

// Scroll to top when route (hash path) changes
function ScrollToTop() {
  const { pathname, hash, search } = useLocation();
  useEffect(() => {
    // For HashRouter, pathname changes with hash-routes as well
    window.scrollTo({ top: 0, left: 0, behavior: "instant" });
  }, [pathname, hash, search]);
  return null;
}

// Simple navigation for quick page switching
function Nav() {
  return (
    <nav className="nav-bar">
      <NavLink to="/" end className="nav-link">Home</NavLink>
      <NavLink to="/income-calc" className="nav-link">手取り額計算</NavLink>
      <NavLink to="/graph" className="nav-link">所得グラフ</NavLink>
    </nav>
  );
}

function NotFound() {
  return (
    <div className="notfound">
      <h2 className="notfound-title">Not Found</h2>
      <p>ページが見つかりませんでした。</p>
      <Link to="/" className="notfound-link">Homeへ戻る</Link>
    </div>
  );
}

function App() {
  return (
    <Router>
      <ScrollToTop />
      <Nav />
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/income-calc" element={<IncomeCalc />} />
        <Route path="/graph" element={<IncomeGraph />} />
        <Route path="*" element={<NotFound />} />
      </Routes>
    </Router>
  );
}
export default App;