// src/pages/IncomeGraph.js
import React, { useMemo, useState, useCallback } from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  ReferenceLine,
} from "recharts";
import { simulateHousehold } from "./IncomeCalc";
import "../App.css";

// 万円⇄円ヘルパ
const WAN = 10000;
const toWan = (yen) =>
  isFinite(yen) ? Math.round(((yen || 0) / WAN) * 10) / 10 : 0;

// ─────────────────────────────────────────────
// 条件フォーム（条件1/2で同じUI）
// ─────────────────────────────────────────────
function ConditionForm({
  title,
  state,
  setState,
  onChildAdd,
  onChildDel,
  onChildUpdate,
}) {
  const nset = (key) => (e) =>
    setState((s) => ({
      ...s,
      [key]: e.target.value === "" ? "" : Number(e.target.value),
    }));
  const sset = (key) => (e) =>
    setState((s) => ({
      ...s,
      [key]: e.target.value,
    }));

  return (
    <div className="card mb">
      <h2 className="mt0">{title}</h2>

      <div className="grid2">
        <div>
          <h3 className="mt0">主／配偶者（年齢・配偶者の障害区分）</h3>
          <div className="row">
            <label>主の年齢</label>
            <input
              type="number"
              step={1}
              value={state.aAge}
              onChange={nset("aAge")}
            />
          </div>
          <div className="row">
            <label>配偶者の年齢</label>
            <input
              type="number"
              step={1}
              value={state.bAge}
              onChange={nset("bAge")}
            />
          </div>
          <div className="row">
            <label>配偶者の障害区分</label>
            <select value={state.bDisability} onChange={sset("bDisability")}>
              <option value="none">なし</option>
              <option value="general">一般</option>
              <option value="special">特別</option>
            </select>
          </div>
        </div>

        <div>
          <h3 className="mt0">世帯年収の分け方</h3>
          <div className="row">
            <label>モード</label>
            <select value={state.mode} onChange={sset("mode")}>
              <option value="ratio">比率で按分（主％）</option>
              <option value="fixedSpouse">配偶者を固定して主で調整</option>
            </select>
          </div>
          {state.mode === "ratio" ? (
            <div className="row">
              <label>主の比率（%）</label>
              <input
                type="number"
                min={0}
                max={100}
                step={1}
                value={state.ratioPct}
                onChange={nset("ratioPct")}
              />
            </div>
          ) : (
            <div className="row">
              <label>配偶者の給与（万円/年）</label>
              <input
                type="number"
                step={1}
                value={state.fixedSpouseWan}
                onChange={nset("fixedSpouseWan")}
              />
            </div>
          )}
        </div>
      </div>

      <div className="grid2">
        <div>
          <h4>主の賞与</h4>
          <div className="row">
            <label>賞与合計（万円/年）</label>
            <input
              type="number"
              step={0.1}
              value={state.bonusA}
              onChange={nset("bonusA")}
            />
          </div>
          <div className="row">
            <label>賞与回数（回/年）</label>
            <input
              type="number"
              min={1}
              max={6}
              step={1}
              value={state.bonusTimesA}
              onChange={nset("bonusTimesA")}
            />
          </div>
        </div>
        <div>
          <h4>配偶者の賞与</h4>
          <div className="row">
            <label>賞与合計（万円/年）</label>
            <input
              type="number"
              step={0.1}
              value={state.bonusB}
              onChange={nset("bonusB")}
            />
          </div>
          <div className="row">
            <label>賞与回数（回/年）</label>
            <input
              type="number"
              min={1}
              max={6}
              step={1}
              value={state.bonusTimesB}
              onChange={nset("bonusTimesB")}
            />
          </div>
        </div>
      </div>

      <h3>子ども</h3>
      <div className="mb flex" style={{ gap: 8, flexWrap: "wrap" }}>
        <button type="button" onClick={onChildAdd}>
          ＋ 子を追加
        </button>
        <button
          type="button"
          onClick={onChildDel}
          disabled={state.children.length === 0}
        >
          － 末尾を削除
        </button>
        <span className="muted">（最大5人）</span>
      </div>
      <div className="scroll-x">
        <table className="table">
          <thead>
            <tr>
              <th>#</th>
              <th>年齢</th>
              <th>年収（万円）</th>
              <th>障害区分</th>
              <th>同居</th>
            </tr>
          </thead>
          <tbody>
            {state.children.map((c, idx) => (
              <tr key={idx}>
                <td>{idx + 1}</td>
                <td>
                  <input
                    type="number"
                    step={1}
                    value={c.age}
                    onChange={(e) => onChildUpdate(idx, "age", e.target.value)}
                  />
                </td>
                <td>
                  <input
                    type="number"
                    step={0.1}
                    value={c.incomeWan}
                    onChange={(e) =>
                      onChildUpdate(idx, "incomeWan", e.target.value)
                    }
                  />
                </td>
                <td>
                  <select
                    value={c.grade}
                    onChange={(e) =>
                      onChildUpdate(idx, "grade", e.target.value)
                    }
                  >
                    <option value="none">なし</option>
                    <option value="1">1級（=特別）</option>
                    <option value="2">2級（=一般）</option>
                  </select>
                </td>
                <td>
                  <input
                    type="checkbox"
                    checked={!!c.cohab}
                    onChange={(e) =>
                      onChildUpdate(idx, "cohab", e.target.checked)
                    }
                  />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      <h3>判定所得に使う「認める控除」</h3>
      <div className="grid2">
        <div>
          <div className="row">
            <label>生命保険料（円）</label>
            <input
              type="number"
              step={1000}
              value={state.lifeInsPaid}
              onChange={nset("lifeInsPaid")}
            />
          </div>
          <div className="row">
            <label>地震保険料（円）</label>
            <input
              type="number"
              step={1000}
              value={state.quakeInsPaid}
              onChange={nset("quakeInsPaid")}
            />
          </div>
          <div className="row">
            <label>小規模企業共済（円）</label>
            <input
              type="number"
              step={1000}
              value={state.smallKPaid}
              onChange={nset("smallKPaid")}
            />
          </div>
        </div>
        <div>
          <div className="row">
            <label>医療費支出（円）</label>
            <input
              type="number"
              step={1000}
              value={state.medPaid}
              onChange={nset("medPaid")}
            />
          </div>
          <div className="row">
            <label>保険金等補填（円）</label>
            <input
              type="number"
              step={1000}
              value={state.medInsRec}
              onChange={nset("medInsRec")}
            />
          </div>
          <div className="row">
            <label>寄附金（円）</label>
            <input
              type="number"
              step={1000}
              value={state.donationPaid}
              onChange={nset("donationPaid")}
            />
          </div>
        </div>
      </div>
    </div>
  );
}

// ─────────────────────────────────────────────
// 本体
// ─────────────────────────────────────────────
export default function IncomeGraph() {
  // X軸（世帯年収）
  const [xMax, setXMax] = useState(1500); // 0〜1500万円
  const [step, setStep] = useState(10); // 10万円刻み

  // 条件1（完全独立）
  const [cond1, setCond1] = useState({
    mode: "ratio", // ratio | fixedSpouse
    ratioPct: 50,
    fixedSpouseWan: 150,
    aAge: 35,
    bAge: 35,
    bDisability: "none", // none|general|special
    bonusA: 0,
    bonusTimesA: 2,
    bonusB: 0,
    bonusTimesB: 2,
    children: [],
    lifeInsPaid: 0,
    quakeInsPaid: 0,
    smallKPaid: 0,
    medPaid: 0,
    medInsRec: 0,
    donationPaid: 0,
  });

  // 条件2（完全独立）
  const [cond2, setCond2] = useState({
    mode: "ratio",
    ratioPct: 50,
    fixedSpouseWan: 150,
    aAge: 35,
    bAge: 35,
    bDisability: "none",
    bonusA: 0,
    bonusTimesA: 2,
    bonusB: 0,
    bonusTimesB: 2,
    children: [],
    lifeInsPaid: 0,
    quakeInsPaid: 0,
    smallKPaid: 0,
    medPaid: 0,
    medInsRec: 0,
    donationPaid: 0,
  });

  // 入力ヘルパ
  const nsetTop = (fn) => (e) =>
    fn(e.target.value === "" ? "" : Number(e.target.value));

  // 世帯年収を主/配偶者に按分
  const splitByRatio = useCallback((totalWan, ratioPct) => {
    const aWan = Math.max(0, Math.round((totalWan * ratioPct) / 100));
    const bWan = Math.max(0, totalWan - aWan);
    return { aWan, bWan };
  }, []);

  // 1条件ぶんの系列を生成
  const buildRowsForCond = useCallback(
    (cond) => {
      const rows = [];
      for (let x = 0; x <= xMax; x += step) {
        let aWan = 0,
          bWan = 0;
        if (cond.mode === "ratio") {
          const { aWan: a, bWan: b } = splitByRatio(x, cond.ratioPct);
          aWan = a;
          bWan = b;
        } else {
          bWan = Math.max(0, Math.min(x, cond.fixedSpouseWan));
          aWan = Math.max(0, x - bWan);
        }

        const sim = simulateHousehold({
          aSalaryWan: aWan,
          aBonusWan: cond.bonusA,
          aBonusTimes: cond.bonusTimesA,
          bSalaryWan: bWan,
          bBonusWan: cond.bonusB,
          bBonusTimes: cond.bonusTimesB,
          aAge: cond.aAge,
          bAge: cond.bAge,
          children: cond.children,
          bDisability: cond.bDisability,
          lifeInsPaid: cond.lifeInsPaid,
          quakeInsPaid: cond.quakeInsPaid,
          smallKPaid: cond.smallKPaid,
          medPaid: cond.medPaid,
          medInsRec: cond.medInsRec,
          donationPaid: cond.donationPaid,
        });

        const hh = sim.household;
        const allow = sim.allowanceYear || 0;
        const bd = sim.allowanceBreakdown || {};

        rows.push({
          x,
          with: toWan(hh.net + allow),
          without: toWan(hh.net),
          allow: toWan(allow),
          tkj: toWan(bd.tokujifuYearly || 0),
          sj: toWan(bd.shojiYearly || 0),
          ts: toWan(bd.tokushoYearly || 0),
        });
      }
      return rows;
    },
    [xMax, step, splitByRatio]
  );

  const data1 = useMemo(() => buildRowsForCond(cond1), [cond1, buildRowsForCond]);
  const data2 = useMemo(() => buildRowsForCond(cond2), [cond2, buildRowsForCond]);

  // 壁（>0 → 0 になる最初の x）
  const findWall = (xs, key) => {
    for (let i = 1; i < xs.length; i++) {
      const prev = xs[i - 1][key] || 0;
      const cur = xs[i][key] || 0;
      if (prev > 0 && cur === 0) return xs[i].x;
    }
    return null;
  };
  const wall1 = {
    tkj: findWall(data1, "tkj"),
    sj: findWall(data1, "sj"),
    ts: findWall(data1, "ts"),
  };
  const wall2 = {
    tkj: findWall(data2, "tkj"),
    sj: findWall(data2, "sj"),
    ts: findWall(data2, "ts"),
  };

  // Recharts は単一 data 配列なので、cond1 をベースに cond2 を合成
  const mergedData = useMemo(() => {
    const byX2 = new Map(data2.map((r) => [r.x, r]));
    return data1.map((r1) => {
      const r2 = byX2.get(r1.x) || {};
      return {
        x: r1.x,
        y1_with: r1.with,
        y1_without: r1.without,
        y2_with: r2.with,
        y2_without: r2.without,
      };
    });
  }, [data1, data2]);

  // クリック/タップで詳細（スマホ対応）
  const [clickedX, setClickedX] = useState(null);
  const handleChartClick = (state) => {
    if (state && state.activeLabel != null) setClickedX(state.activeLabel);
  };
  const row1 = clickedX != null ? data1.find((r) => r.x === clickedX) : null;
  const row2 = clickedX != null ? data2.find((r) => r.x === clickedX) : null;

  // ── CSV出力（詳細＝cond1/cond2の手当内訳まで含む）
  const exportRows = useMemo(() => {
    const byX2 = new Map(data2.map((r) => [r.x, r]));
    return data1.map((r1) => {
      const r2 = byX2.get(r1.x) || {};
      return {
        x: r1.x,
        // 条件1
        y1_with: r1.with,
        y1_without: r1.without,
        y1_allow: r1.allow,
        y1_tkj: r1.tkj,
        y1_sj: r1.sj,
        y1_ts: r1.ts,
        // 条件2
        y2_with: r2.with,
        y2_without: r2.without,
        y2_allow: r2.allow,
        y2_tkj: r2.tkj,
        y2_sj: r2.sj,
        y2_ts: r2.ts,
      };
    });
  }, [data1, data2]);

  function toCSV(rows) {
    const headers = [
      "x",
      "y1_with", "y1_without", "y1_allow", "y1_tkj", "y1_sj", "y1_ts",
      "y2_with", "y2_without", "y2_allow", "y2_tkj", "y2_sj", "y2_ts",
    ];
    const lines = [headers.join(",")];
    for (const r of rows) {
      lines.push(headers.map((h) => (r[h] ?? "")).join(","));
    }
    return "\uFEFF" + lines.join("\n"); // BOM付き
  }

  function downloadCSV() {
    const csv = toCSV(exportRows);
    const blob = new Blob([csv], { type: "text/csv;charset=utf-8;" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    const pad = (n) => String(n).padStart(2, "0");
    const d = new Date();
    a.href = url;
    a.download = `income_graph_${d.getFullYear()}${pad(
      d.getMonth() + 1
    )}${pad(d.getDate())}_${pad(d.getHours())}${pad(
      d.getMinutes()
    )}${pad(d.getSeconds())}.csv`;
    document.body.appendChild(a);
    a.click();
    a.remove();
    URL.revokeObjectURL(url);
  }

  return (
    <div className="App page">
      <h1 className="App-title">所得グラフ</h1>
      <p className="App-text">
        条件1と条件2を<strong>別々に同じUI</strong>で設定して比較します。上側に各手当の「所得制限の壁」を表示。グラフをクリック/タップすると下に詳細が出ます。
      </p>

      {/* X軸設定 */}
      <div className="card mb">
        <h2 className="mt0">X軸（世帯年収）</h2>
        <div className="grid2">
          <div className="row">
            <label>最大（万円）</label>
            <input
              type="number"
              step={10}
              value={xMax}
              onChange={nsetTop(setXMax)}
            />
          </div>
          <div className="row">
            <label>刻み（万円）</label>
            <input
              type="number"
              step={1}
              value={step}
              onChange={nsetTop(setStep)}
            />
          </div>
        </div>
      </div>

      {/* 条件1 */}
      <ConditionForm
        title="条件1"
        state={cond1}
        setState={setCond1}
        onChildAdd={() =>
          setCond1((s) =>
            s.children.length < 5
              ? {
                  ...s,
                  children: [
                    ...s.children,
                    { age: "", incomeWan: "", grade: "none", cohab: false },
                  ],
                }
              : s
          )
        }
        onChildDel={() =>
          setCond1((s) => ({ ...s, children: s.children.slice(0, -1) }))
        }
        onChildUpdate={(i, k, v) =>
          setCond1((s) => ({
            ...s,
            children: s.children.map((c, idx) =>
              idx === i ? { ...c, [k]: v } : c
            ),
          }))
        }
      />

      {/* 条件2 */}
      <ConditionForm
        title="条件2"
        state={cond2}
        setState={setCond2}
        onChildAdd={() =>
          setCond2((s) =>
            s.children.length < 5
              ? {
                  ...s,
                  children: [
                    ...s.children,
                    { age: "", incomeWan: "", grade: "none", cohab: false },
                  ],
                }
              : s
          )
        }
        onChildDel={() =>
          setCond2((s) => ({ ...s, children: s.children.slice(0, -1) }))
        }
        onChildUpdate={(i, k, v) =>
          setCond2((s) => ({
            ...s,
            children: s.children.map((c, idx) =>
              idx === i ? { ...c, [k]: v } : c
            ),
          }))
        }
      />

      {/* グラフ */}
      <div className="card mb">
        <div
          className="flex"
          style={{ justifyContent: "space-between", alignItems: "center", gap: 12 }}
        >
          <h2 className="mt0" style={{ marginBottom: 0 }}>
            手取り比較グラフ
          </h2>
          <button type="button" onClick={downloadCSV}>CSVダウンロード</button>
        </div>

        <div style={{ width: "100%", height: 460 }}>
          <ResponsiveContainer>
            <LineChart
              data={mergedData}
              margin={{ top: 24, right: 24, left: 10, bottom: 12 }}
              onClick={handleChartClick}
            >
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis
                dataKey="x"
                type="number"
                domain={[0, xMax]}
                tickCount={Math.min(16, Math.floor(xMax / step) + 1)}
                label={{
                  value: "世帯年収（万円）",
                  position: "insideBottom",
                  offset: -5,
                }}
              />
              <YAxis
                domain={[0, "dataMax"]}
                label={{ value: "手取り（万円）", angle: -90, position: "insideLeft" }}
              />
              <Tooltip labelFormatter={(label) => `世帯年収：${label} 万円`} />
              <Legend />

              {/* 所得制限の壁（条件1：赤系） */}
              {wall1.tkj != null && (
                <ReferenceLine
                  x={wall1.tkj}
                  stroke="#d32f2f"
                  strokeDasharray="4 4"
                  label={{ value: "特児扶(1)", position: "top" }}
                />
              )}
              {wall1.sj != null && (
                <ReferenceLine
                  x={wall1.sj}
                  stroke="#f44336"
                  strokeDasharray="4 4"
                  label={{ value: "障児手(1)", position: "top" }}
                />
              )}
              {wall1.ts != null && (
                <ReferenceLine
                  x={wall1.ts}
                  stroke="#ef9a9a"
                  strokeDasharray="4 4"
                  label={{ value: "特障手(1)", position: "top" }}
                />
              )}

              {/* 所得制限の壁（条件2：青系） */}
              {wall2.tkj != null && (
                <ReferenceLine
                  x={wall2.tkj}
                  stroke="#1976d2"
                  strokeDasharray="4 4"
                  label={{ value: "特児扶(2)", position: "top" }}
                />
              )}
              {wall2.sj != null && (
                <ReferenceLine
                  x={wall2.sj}
                  stroke="#2196f3"
                  strokeDasharray="4 4"
                  label={{ value: "障児手(2)", position: "top" }}
                />
              )}
              {wall2.ts != null && (
                <ReferenceLine
                  x={wall2.ts}
                  stroke="#90caf9"
                  strokeDasharray="4 4"
                  label={{ value: "特障手(2)", position: "top" }}
                />
              )}

              {/* 条件1：色=マゼンタ系 */}
              <Line
                type="monotone"
                dataKey="y1_with"
                name="条件1：手当あり"
                dot={false}
                strokeWidth={2}
                stroke="#e91e63"
              />
              <Line
                type="monotone"
                dataKey="y1_without"
                name="条件1：手当なし"
                dot={false}
                strokeWidth={2}
                strokeDasharray="6 6"
                stroke="#e91e63"
              />

              {/* 条件2：色=ブルー系 */}
              <Line
                type="monotone"
                dataKey="y2_with"
                name="条件2：手当あり"
                dot={false}
                strokeWidth={2}
                stroke="#3f51b5"
              />
              <Line
                type="monotone"
                dataKey="y2_without"
                name="条件2：手当なし"
                dot={false}
                strokeWidth={2}
                strokeDasharray="6 6"
                stroke="#3f51b5"
              />
            </LineChart>
          </ResponsiveContainer>
        </div>
        <div className="muted" style={{ marginTop: 8 }}>
          ※「壁」は簡易検出（手当額が正から0へ落ちる最初のX）。厳密な限度額ラインを常に表示したい場合は
          <code>simulateHousehold</code> の返り値に閾値（本人/配偶者等）を追加して参照してください。
        </div>
      </div>

      {/* クリック/タップの詳細（スマホ対応） */}
      <div className="card">
        <h2 className="mt0">クリック位置の詳細</h2>
        {clickedX == null && (
          <div className="muted">
            グラフをクリック / タップすると、この欄に条件1/2の詳細が表示されます。
          </div>
        )}
        {clickedX != null && (
          <>
            <div>
              世帯年収：<b>{clickedX}</b> 万円
            </div>
            <div className="grid2">
              <div>
                <h4 className="mt0">条件1</h4>
                {row1 ? (
                  <ul className="muted">
                    <li>
                      手当あり：<b>{row1.with?.toLocaleString()} 万円</b>
                    </li>
                    <li>手当なし：{row1.without?.toLocaleString()} 万円</li>
                    <li>手当合計：{row1.allow?.toLocaleString()} 万円</li>
                    <li> ├ 特児扶：{row1.tkj?.toLocaleString()} 万円</li>
                    <li> ├ 障児手：{row1.sj?.toLocaleString()} 万円</li>
                    <li> └ 特障手：{row1.ts?.toLocaleString()} 万円</li>
                  </ul>
                ) : (
                  <div className="muted">データなし</div>
                )}
              </div>
              <div>
                <h4 className="mt0">条件2</h4>
                {row2 ? (
                  <ul className="muted">
                    <li>
                      手当あり：<b>{row2.with?.toLocaleString()} 万円</b>
                    </li>
                    <li>手当なし：{row2.without?.toLocaleString()} 万円</li>
                    <li>手当合計：{row2.allow?.toLocaleString()} 万円</li>
                    <li> ├ 特児扶：{row2.tkj?.toLocaleString()} 万円</li>
                    <li> ├ 障児手：{row2.sj?.toLocaleString()} 万円</li>
                    <li> └ 特障手：{row2.ts?.toLocaleString()} 万円</li>
                  </ul>
                ) : (
                  <div className="muted">データなし</div>
                )}
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
}